import React from 'react'
import axios from 'axios'
import Navbar from './components/Navbar/Navbar'
// import Search from './components/Navbar/Search/Search'
import Container from './components/Container/Container'
import './css/app.style.min.css'

const apiurl =
	'https://api.themoviedb.org/3/search/movie?api_key=07a61de5b731a869bc9cec8e25d2c8a8&query='

export default function App() {
	const [searchTerm, setSearchTerm] = React.useState('')

	const [searchResults, setSearchResults] = React.useState({
		results: {},
	})

	const handleChange = (event) => {
		setSearchTerm(event.target.value)
		// console.log(event.target.value)
	}

	React.useEffect(() => {
		axios(apiurl + (searchTerm === '' ? 'a' : searchTerm))
			.then((response) => response)
			.then((data) => {
				setSearchResults((prev) => {
					return { ...prev, results: data.data.results }
				})
			})
	}, [searchTerm])

	return (
		<div className='App'>
			<Navbar value={searchTerm} onChange={handleChange} />
			<Container value={searchResults} />
		</div>
	)
}
