import React, { useRef } from 'react'
import './css/modalcontent.style.min.css'
import StarRatings from 'react-star-ratings'

export default function ModalContent({ props, close, imgSrc }) {
	console.log(props)

	return (
		<div className='modal'>
			<button className='modal-button1' onClick={close}>
				&times;
			</button>
			<div className='modal-block'>
				<p className='modal-header'>{props.title}</p>

				<div className='modal-block1'>
					<img
						src={imgSrc}
						alt='Movie card'
						className='modal-image'
					/>
					<div className='modal-block1-part2'>
						<StarRatings
							rating={props.vote_average / 2}
							starDimension='25'
							starSpacing='2'
							starRatedColor='yellow'
							numberOfStars={5}
						/>
						<p>Release date: {props.release_date}</p>

						<p>
							Original Language:{' '}
							{props.original_language.toUpperCase()}
						</p>

						{props.adult && <p>All Public: No</p>}
						{!props.adult && <p>All public: Yes</p>}
					</div>
				</div>
				<p className='modal-overview'>Overview:</p>

				<p className='modal-content'>{props.overview}</p>
				<br />
				<p className='modal-average modal-font-size'>
					Vote average: {props.vote_average}
				</p>
				<br />
				<button
					className='modal-button2'
					onClick={() => {
						close()
					}}
				>
					Close
				</button>
				<br />
			</div>
		</div>
	)
}
