import React from 'react'
import MovieCard from './Movie-Card/MovieCard'
import './css/movielist.style.min.css'
export default function Movielist(props) {
	return (
		<ul>
			{Object.keys(props.value.results).map((key) => (
				<MovieCard
					key={key}
					index={key}
					value={props.value.results[key]}
				/>
			))}
		</ul>
	)
}
