import React, { useState } from 'react'
import Popup from 'reactjs-popup'
import ModalContent from './Modal-Content/ModalContent'
import './css/moviemodal.style.min.css'

export default function MovieModal(props) {
	const [posterBaseUrl] = useState(' http://image.tmdb.org/t/p/w185')

	return (
		<Popup
			trigger={
				<button className='modal-button'>
					{props.props.poster_path && (
						<img
							src={posterBaseUrl + props.props.poster_path}
							alt={'not found'}
						/>
					)}
					<p className='modal-title'>{props.props.title}</p>
				</button>
			}
			modal
			lockScroll={true}
			closeOnDocumentClick
		>
			{/*Modal content*/}
			{(close) => (
				<ModalContent
					props={props.props}
					close={close}
					imgSrc={posterBaseUrl + props.props.poster_path}
				/>
			)}
		</Popup>
	)
}
